@import url(https://fonts.googleapis.com/css?family=Playfair+Display:900|Roboto:500&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    margin: 0;
    padding: 0;
}

.App-header {
    background: #333333;
    height: 10vh;
    margin: 0;
    padding: 10vh;
    vertical-align: middle;
    text-align: center;
}

.header-title{
    font-family: 'Roboto';
    font-size: 80px;
    color: #ffffff;
    margin: 0;
    padding: 0;
}

.App-link {
  color: #61dafb;
}

.ScrollMarqueeContainer{
    margin-top: 5vh;
    height: 40vw;
    background: #eeeeee;
    overflow: hidden;
    white-space: nowrap;
    padding-top: 10vh;
}


.SlideInBoxes{
    display: block;
    height: 75vw;
    background: #cccccc;
    padding: 0;
    margin: 5vh 0;
    text-align: center;
    vertical-align: middle;
}

@-webkit-keyframes section1-left-box-show{
    from{
        margin-left:  0px;
        opacity: 0;
    }

    to{
        margin-left: 10vw;
        opacity: 1;
    }
}

@keyframes section1-left-box-show{
    from{
        margin-left:  0px;
        opacity: 0;
    }

    to{
        margin-left: 10vw;
        opacity: 1;
    }
}

@-webkit-keyframes section1-right-box-show{
    from{
        margin-right:  0px;
        opacity: 0;
    }

    to{
        margin-right: 10vw;
        opacity: 1;
    }
}

@keyframes section1-right-box-show{
    from{
        margin-right:  0px;
        opacity: 0;
    }

    to{
        margin-right: 10vw;
        opacity: 1;
    }
}

@-webkit-keyframes section1-box-hide{
    from{
        opacity: 1;
    }

    to{
        opacity: 0;
    }
}

@keyframes section1-box-hide{
    from{
        opacity: 1;
    }

    to{
        opacity: 0;
    }
}

.SlideInBoxes .box{
    width: 35vw;
    height: 35vw;
    vertical-align: middle;
    margin-top: 20vw;
    font-size: 40vh;
    background: #222222;
    color: #DDDDDD;
}

.SlideInBoxes .box.left{
    float: left;
    opacity: 0;
    margin-left: 10vw;
}

.SlideInBoxes.animate-show .left{
    -webkit-animation: section1-left-box-show 0.5s forwards;
            animation: section1-left-box-show 0.5s forwards;
}

.SlideInBoxes .box.right{
    float: right;
    opacity: 0;
    margin-right: 10vw;
}

.SlideInBoxes.animate-show .right{
    -webkit-animation: section1-right-box-show 0.5s forwards;
            animation: section1-right-box-show 0.5s forwards;
}

.SlideInBoxes.animate-hide .box
{
    -webkit-animation: section1-box-hide 0.5s forwards;
            animation: section1-box-hide 0.5s forwards;
}


.ScrollMarquee .marqueeText{
    font-family: 'Playfair Display';
    font-size: 18vw;
    font-weight: bold;
    line-height: 0.9em;
    color: #000000;
}

